/* eslint-disable @typescript-eslint/no-explicit-any */
import { ExtractPropTypes, PropType } from 'vue';

export type SelectOption = {
  children?: SelectOption[],
  [key: string]: any,
}

export type SelectOptions = SelectOption[]

export type selectApiType = '';

export const selectProps = {
  /**
   * config-provider
   */
  arrayName: String,
  /**
   * children
   */
  group: Boolean,
  /**
   * 
   */
  defaultValue: String,
  /**
   * 
   */
  immediate: {
    type: Boolean,
    default: true,
  },
  /**
   * readonly
   */
  onlyDisplay: Boolean,
  /**
   * valuekey
   */
  optionKey: {
    type: String,
    default: 'value',
  },
  /**
   * 
   */
  width: [String, Number],
  modelValue: [String, Array],
  /**
   * promise
   */
  api: {
    type: Object as PropType<() => Promise<ApiResponseType>>,
    validator(val: () => unknown | selectApiType): boolean {
      const allow = ['users', 'groups', 'ips', 'shterm'];
      return typeof val === 'function' || allow.includes(val)
    }
  },
  /**
   * 
   */
  options: {
    type: Array as PropType<SelectOptions>,
  },
  /**
   * config-provider
   */
  customLabel: {
    type: String,
  },
  /**
   * config-provider
   */
  customValue: {
    type: String,
  },
  /**
   * 
   */
  itemDisabled: Function as PropType<(val: unknown) => boolean>,
}

export type SelectProps = Partial<ExtractPropTypes<typeof selectProps>>;
