<template>
  <bc-dialog width="480px" v-model="dialogVisible" title="" append-to-body noFooter>
    <section v-loading="uploading">
      <el-alert type="warning" :closable="false">
        <template #default>
          <div class="text-center">
            <span>Excel</span><br>
            <el-link type="primary" @click="handleDownloadTemplate">Excel?</el-link>
          </div>
        </template>
      </el-alert>
      <el-upload
        style="text-align: center; margin-top: 20px"
        action=""
        :ref="uploadRef"
        :show-file-list="false"
        with-credentials
        :httpRequest="uploadFile"
        accept=".xlsx,.xls"
        drag
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <!-- <div class="el-upload__tip" slot="tip">excel</div> -->
      </el-upload>
    </section>
  </bc-dialog>
</template>

<script lang="ts">
import { UploadFile } from 'element-plus/lib/components/upload/src/upload.type';
import { defineComponent, computed, ref } from 'vue';
import { uploadProps } from './type';
export default defineComponent({
  name: 'BcUpload',
  emits: ['update:modelValue', 'success', 'error'],
  props: uploadProps,
  setup(props, context) {
    const uploading = ref(false);
    const uploadRef = ref();

    const dialogVisible = computed({
      get() {
        return props.modelValue;
      },
      set(visible) {
        context.emit('update:modelValue', visible);
      }
    });

    function uploadFile() {
      uploading.value = true;
      const upload = uploadRef.value;
      const blobList: UploadFile[] = upload.uploadFiles;
      const form = new FormData();
      blobList.forEach(item => {
        form.append('file', item.raw);
      })
      props.uploadApi?.(form).then(() => {
        uploading.value = false;
        upload.clearFiles();
        context.emit('success');
        context.emit('update:modelValue', false);
      }).catch(() => {
        uploading.value = false;
        upload.clearFiles();
        context.emit('error');
      })
    }
    function handleDownloadTemplate() {
      props.templateApi?.();
    }
    return {
      dialogVisible,
      uploading,
      uploadRef,

      uploadFile,
      handleDownloadTemplate
    }
  }
})
</script>

<style lang="scss" scoped>
.text-info {
  cursor: pointer;
}
</style>