import { ExtractPropTypes } from 'vue';
export const buttonProps = {
  /**
   * 
   */
  confirm: [Boolean, String],
  /**
   * mini
   */
  tooltip: Boolean,
  /**
   * svg-sprite
   */
  icon: String,
  /**
   * tooltip
   */
  mini: Boolean,
  /**
   * 
   */
  type: {
    type: String,
    default: null,
  }
}

export type ButtonProps = Partial<ExtractPropTypes<typeof buttonProps>>;