import { PropType, ExtractPropTypes } from '@vue/runtime-core';
import { TableColumnCtx } from 'element-plus/lib/components/table/src/table-column/defaults';
export type CellType = {
  row: Record<string, unknown>,
  rowIndex: number,
  column: TableColumnCtx<unknown>,
  columnIndex: number,
};

export type Colspanoptions = {
  includes: string[],
  parentProp: string,
}

export const tableProps = {
  /**
   * config-providerrows
   */
  arrayName: {
    type: String,
    default: '',
  },
  /**
   * 
   */
  colspanOptions: {
    type: Object as PropType<Colspanoptions>,
    default: () => ({}),
  },
  /**
   * 
   */
  padding: {
    type: Boolean,
    default: true,
  },
  /**
   * elementui table
   */
  custom: Boolean,
  /**
   * 
   */
  data: Array,
  /**
   * 
   */
  filter: Function,
  /**
   * created
   */
  immediate: {
    type: Boolean,
    default: true,
  },
  /**
   * 
   */
  pagination: Boolean,
  /**
   * promise
   */
  api: Function,
  /**
   * 
   */
  params: {
    type: Object as PropType<Record<string, unknown>>,
    default: () => ({ page: 1, rows: 20 })
  },
  /**
   * 
   */
  config: {
    type: Array as PropType<Record<string, unknown>[]>,
    default: () => [],
  },
  /**
   * 
   */
  total: {
    type: Number,
    default: 0,
  },
  /**
   * Beta
   */
  fixXScroll: Boolean,
  /**
   * 
   */
  simple: Boolean,
  /**
   * loading
   */
  load: {
    type: Boolean,
    default: true,
  }
}

export type TableProps = Partial<ExtractPropTypes<typeof tableProps>>;
